<template>
  <section v-if="trip && owner" class="trip-details">
    <div class="AppHeader">
      <AppHeader />
    </div>
    <div class="map">
      <google-map :location="trip.destination" />
    </div>
    <div class="trip-chat">
      <h3>Chat</h3>
    </div>
    <div class="trip-desc">
      <h3>{{trip.destination}}</h3>
      <button @click="joinTrip">Join</button>
      <h4>{{trip.title}}</h4>
      <h4>{{trip.participantCap}}</h4>
      <h4>Owner: {{owner.firstName}} {{owner.lastName}}</h4>
      <img class="user-img" :src="owner.imgUrl" />
      <router-link
        v-if="loggedInUser && loggedInUser._id === trip.owner._id"
        :to="'/TripEdit/'+trip._id"
      >Edit</router-link>
      <button
        v-if="loggedInUser && loggedInUser._id === trip.owner._id"
        @click="remove(trip._id)"
      >Remove Trip</button>
    </div>
    <div class="trip-plan">
      <p>dsfdsfdsfdsf</p>
      <p>dsfdsfdsfdsf</p>
      <p>dsfdsfdsfdsf</p>
      <p>dsfdsfdsfdsf</p>
    </div>
    <div class="trip-weather">
      <h3>Wether</h3>
    </div>
    <div class="trip-participant">
      <ul>
        <li v-for="(user,i) in this.waitingList" :key="i">{{user.firstName}}{{user.lastName}}</li>
      </ul>
    </div>
  </section>
</template>


<script>
import AppHeader from "../components/Header";
import GoogleMap from "../components/GoogleMap";
export default {
  data() {
    return {
      trip: null,
      owner: null
    };
  },
  async created() {
    const tripId = this.$route.params.id;
    this.trip = await this.$store.dispatch({ type: "getById", tripId });
    this.owner = await this.$store.dispatch({
      type: "getUserById",
      id: this.trip.owner._id
    });
    if (this.trip.waitingList) {
      await this.$store.dispatch({
        type: "getWaitingListUsers",
        tripId: { tripId: this.trip._id }
      });
    }
  },
  computed: {
    loggedInUser() {
      return this.$store.getters.loggedInUser;
    },
    waitingList() {
      return this.$store.getters.waitingList;
    }
  },
  methods: {
    async remove(id) {
      try {
        await this.$store.dispatch({ type: "remove", id });
        this.$router.push("/");
      } catch (err) {
        console.log('not remove',err);
      }
    },
    async joinTrip() {
      if (!this.loggedInUser) {
        this.$router.push("/login");
        return;
      }
      this.trip.waitingList.push({userId: this.loggedInUser._id});
      this.loggedInUser.pending.push({ tripId: this.trip._id });
      try {
        const newTrip = await this.$store.dispatch({
          type: "save",
          trip: this.trip
        });
      } catch (err) {
        console.log('not saved',err);
      }
      try {
        await this.$store.dispatch({
          type: "updateUser",
          user: this.loggedInUser
        });
      } catch (err) {
        console.log('not update',err);
      }
    }
  },
  components: {
    AppHeader,
    GoogleMap
  }
};
</script>




<style lang="scss" scoped>
button {
  background-color: white;
  padding: 5px;
  border: black 1px solid;
}

.trip-details {
  // height: 100vh;
  display: grid;
  grid-gap: 10px;
  grid-template-rows: 0.1fr 4fr 1fr 2fr 2fr;
  grid-template-columns: 2fr 1fr;
  grid-template-areas:
    "AppHeader AppHeader"
    "map map"
    "trip-desc trip-chat"
    "trip-plan trip-chat"
    "trip-weather trip-participant";
}
.AppHeader {
  grid-area: AppHeader;
}
.map {
  grid-area: map;
}
.trip-desc {
  grid-area: trip-desc;
  background-color: lightgreen;
}
.trip-chat {
  grid-area: trip-chat;
  background-image: url("../../public/img/chat.png");
  background-size: cover;
}
.trip-plan {
  grid-area: trip-plan;
  background-color: lightblue;
}
.trip-weather {
  grid-area: trip-weather;
  background-image: url("../../public/img/Weather.png");
  background-size: cover;
}
.trip-participant {
  grid-area: trip-participant;
}
.user-img {
  cursor: pointer;
  width: 30px;
  margin-left: 20px;
  border-radius: 50%;
}
</style>
