<template>
  <div>
    <gmap-map v-if="center" :center="center" :zoom="13" style="width:100%;  height: 400px;">
      <gmap-marker
        :key="index"
        v-for="(m, index) in markers"
        :position="m.position"
        @click="center=m.position"
      ></gmap-marker>
    </gmap-map>
  </div>
</template>

<script>
import MapService from "../Services/MapService";
export default {
  name: "GoogleMap",
  data() {
    return {
      center: null,
      markers: [],
      places: [],
      currentPlace: null
    };
  },

  props: {
    location: String
  },
  async created() {
    console.log(this.location);
    const pos = await MapService.getCoordsByLocation(this.location);
    this.center = pos;
    this.markers.push({ position: pos });
    this.center = pos;
    // await this.geolocate(pos);
  },
  methods: {}
};
</script>