function getCoordsByLocation(pos) {
    pos = pos.replace(/,|'| /, "+")
    return fetch(`https://maps.googleapis.com/maps/api/geocode/json?address=${pos}&key=AIzaSyAY3sSuIsbs3ZDKn_2_-xGEJHgOW8Wmrdk`)
        .then(res => {
            return res.json()
                .then(res => {
                    
                    return res.results[0].geometry.location
                })
        })
}